body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.j-text{
    font-size: 15px;
    text-align: justify;
    line-height: 30px;
    color: #5E3791;
    margin-bottom: 40px;
}
.headline {
    color: #FF4A60;
    margin-bottom: 40px;
}
.jumbotron {
    background-color: #FED163;
}
p {
    font-family: 'Noto Sans', sans-serif;

}
.modal-content {
    font-size: 1rem;
}
.modals {
    padding: 25px 25px;
}
@media (min-width: 576px){
.modal-dialog {
    max-width: 675px;
}
}
.card {
    display: inline-flex;
    margin: 0 25px;
    width: 350px;
    background-color: #FF7081;
}
.btn-primary {
    background-color: #00F1AB;
    border-color:  #00F1AB;
}
.card-txt {
    height: 125px;
}
.spacer {
    margin-bottom: 40px;
}
.modal-content {
    padding: 10px;
}
.btn {
    color: rgb(33, 37, 41);
}
.outer-card {
    padding-bottom: 25px;
}
.app {
    margin: 0 25px;
  }

.outer {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: center;

}
.inner {
    font-size: 1rem;
}
.center-text {
    text-align: center;
}
.center {
    justify-content: center;
}
.width {
    width: 877px;
}
